




import Vue from 'vue';

export default Vue.extend({
  data: () => ({
    showText: ['AND', 'OR'],
    showIndex: 0,
  }),
  methods: {
    click() {
      this.showIndex = (this.showIndex + 1) % this.showText.length;
      this.$emit('change', this.showText[this.showIndex]);
    },
  },
  mounted() {
    this.$emit('change', this.showText[this.showIndex]);
  },
});
